*{
  font-family: 'Josefin Slab', serif;
  font-weight: 500;
  border-radius: 5px;
}

.navbar-brand{
  font-size: 35px;
}
#close{
  display: flex;
  position: absolute;
  top: 30px;
  left: 30px;
  color: #222;
  font-size: 25px;
}
.navbar{
  box-shadow:  0 0 60px 0 rgba(0, 0, 0, 0.1);
  padding: 0 0 0 20px;
  border: 5px solid transparent;
  border-radius: 5px;
  z-index: 999;
  position: sticky;
  top: 0;
  right: 0;
  font-weight: 700;
}
#nav.active{
  right: 0px;
}
.navbar-brand{
  font-size: 45px;
  font-family: 'Dancing Script', cursive;
}
.nav-link{
  font-size: 22px;
  margin: 20px;
  transition: 0.3s;
}
.nav-link:hover{
  font-size: 26px;
  font-weight: bolder;
}
.cont{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.time{
  background-color: #fff;
  padding: 20px;
}
.btn{
  cursor: pointer;
}
.btn:hover{
  font-size: larger;
  transition: 0.3s;
}